import React, { Component } from 'react'
import { NavLink, Route, Switch } from 'react-router-dom'

import loadable from '@loadable/component'
import { CloudDownloadIcon, SearchIcon, ArchiveIcon, SidebarTab } from 'evergreen-ui'
const SearchCmp = loadable((props) => import('./Search'))

class PageTemplates extends Component {
  constructor(props)
  {
    super(props)
    this.state = {
      activePath: window.location.pathname,
      ready: false
    }
    this.updateActivePath = this.updateActivePath.bind(this)
  }
  async componentDidMount()
  {
    this.setState({
      ready: true
    })
  }
  updateActivePath(match, location)
  {
    if (match && this.state.activePath !== location.pathname) {
      requestAnimationFrame(() => {
        this.setState({
          activePath: location.pathname
        })
      });
    }
  }
  render() {
    return (
      <section>
        <nav className="sidenav">
          <NavLink className="sidenav__item" activeClassName='--active' to='/templates' isActive={(match, location) => {this.updateActivePath(match, location)}}>
            <SidebarTab appearance="minimal" isSelected={this.state.activePath === `/templates`}><SearchIcon/> Search</SidebarTab>
          </NavLink>
          <NavLink className="sidenav__item" activeClassName='--active' to='/templates/installed' isActive={(match, location) => {this.updateActivePath(match, location)}}>
            <SidebarTab appearance="minimal" isSelected={this.state.activePath === `/templates/installed`}><CloudDownloadIcon/> Installed</SidebarTab>
          </NavLink>
          <NavLink className="sidenav__item" activeClassName='--active' to='/templates/my-account' isActive={(match, location) => {this.updateActivePath(match, location)}}>
            <SidebarTab appearance="minimal" isSelected={this.state.activePath === `/templates/my-account`}><ArchiveIcon/> My Account</SidebarTab>
          </NavLink>
        </nav>
        <Switch>
          <Route path='/templates' exact={true} component={loadable(() => import('./Search'))}/>
          <Route path='/templates/installed' component={loadable(() => import('./Installed'))}/>
          <Route path='/templates/my-account' component={loadable(() => import('./MyAccount'))}/>
          <Route path='/templates/:type' exact={true} component={props=>(<SearchCmp {...props}/>)}/>
        </Switch>
      </section>
    )
  }
}

export default PageTemplates;
