import React, { Component } from 'react'
import { NavLink, Route, Redirect, Switch } from 'react-router-dom'

import loadable from '@loadable/component'
import { SidebarTab } from 'evergreen-ui'

class PageTools extends Component {
  constructor(props)
  {
    super(props)
    this.state = {
      activePath: window.location.pathname,
      ready: false
    }
    this.updateActivePath = this.updateActivePath.bind(this)
  }
  async componentDidMount()
  {
    this.setState({
      ready: true
    })
  }
  updateActivePath(match, location)
  {
    if (match && this.state.activePath !== location.pathname) {
      requestAnimationFrame(() => {
        this.setState({
          activePath: location.pathname
        })
      });
    }
  }
  menuStruct = [
    {label:'Activity', key:'activity'},
    {label:'Email Debugger', key:'email-debugger'},
    {label:'Run Tests', key:'test-classes'},
    {label:'Deploy', key:'deploy'}
  ]
  render() {
    return (
      <section>
        <nav className="sidenav">
          {
            this.menuStruct.map((item, index)=>(
              <NavLink key={index} className="sidenav__item" activeClassName='--active' to={`/tools/${item.key}`} isActive={(match, location) => {this.updateActivePath(match, location)}}>
                <SidebarTab appearance="minimal" isSelected={this.state.activePath === `/tools/${item.key}`}>{item.label}</SidebarTab>
              </NavLink>
            ))
          }
        </nav>
        <Switch>
          <Route
            exact
            path="/tools"
            render={() => (<Redirect to="/tools/activity" />)}
          />
          <Route path='/tools/email-debugger' component={loadable(() => import('./EmailDebugger'))}/>
          <Route path='/tools/test-classes' component={loadable(() => import('./TestClasses'))}/>
          <Route path='/tools/deploy' component={loadable(() => import('./Deploy'))}/>
          <Route path='/tools/activity' component={loadable(() => import('./Activity'))}/>
        </Switch>
      </section>
    )
  }
}

export default PageTools;
