import React, { Component } from 'react'
import { NavLink, Route, Switch } from 'react-router-dom'

import loadable from '@loadable/component'

import { SidebarTab, DashboardIcon, Pane, FormIcon, CodeIcon, MediaIcon, NewObjectIcon, NewLayersIcon, PanelTableIcon, CubeAddIcon, DocumentIcon } from 'evergreen-ui'
import Busy from '../../@components/Busy'
import ConfigService from '../../@services/Config'

const OverviewCmp = loadable((props) => import('./Overview'))
const SchemaEditCmp = loadable((props) => import('./SchemaEdit'))
const SchemaListCmp = loadable((props) => import('./SchemaList'))

class PageConfig extends Component {
  constructor(props)
  {
    super(props)
    this.state = {
      activePath: window.location.pathname,
      ready: false
    }
    this.updateActivePath = this.updateActivePath.bind(this)
  }
  async componentDidMount()
  {
    ConfigService.getConfig()
      .then((config)=>{
        this.setState({
          ready: true
        })
      })
  }
  updateActivePath(match, location)
  {
    if (match && this.state.activePath !== location.pathname) {
      requestAnimationFrame(() => {
        this.setState({
          activePath: location.pathname
        })
      });
    }
  }
  menuStruct = [
    {label:'Scripts', key:'scripts', icon: (<CodeIcon/>)},
    {label:'Styles', key:'styles', icon: (<MediaIcon/>)},
    {label:'Post Types', key:'types', icon: (<NewObjectIcon/>)},
    {label:'Taxonomies', key:'taxonomies', icon: (<NewLayersIcon/>)},
    {label:'Custom Fields', key:'fields', icon: (<PanelTableIcon/>)},
    {label:'Blocks', key:'blocks', icon: (<CubeAddIcon/>)},
    {label:'Pages', key:'pages', icon: (<DocumentIcon/>)}
  ]
  render() {
    return (
      <section>
        {this.state.ready?(
          <>
            <nav className="sidenav">
              <NavLink className="sidenav__item" activeClassName='--active' to={`/${this.props.match.params.siteKey}/config`} exact={true} isActive={(match, location) => {this.updateActivePath(match, location)}}>
                <SidebarTab appearance="minimal" isSelected={this.state.activePath === `/${this.props.match.params.siteKey}/config`}><DashboardIcon/>Overview</SidebarTab>
              </NavLink>
              <NavLink className="sidenav__item" activeClassName='--active' to={`/${this.props.match.params.siteKey}/config/settings`} exact={true} isActive={(match, location) => {this.updateActivePath(match, location)}}>
                <SidebarTab appearance="minimal" isSelected={this.state.activePath === `/${this.props.match.params.siteKey}/config/settings`}><FormIcon/> Settings</SidebarTab>
              </NavLink>
              {
                this.menuStruct.map((item, index)=>(
                  <NavLink key={index} className="sidenav__item" activeClassName='--active' to={`/${this.props.match.params.siteKey}/config/${item.key}`} isActive={(match, location) => {this.updateActivePath(match, location)}}>
                    <SidebarTab appearance="minimal" isSelected={this.state.activePath.startsWith(`/${this.props.match.params.siteKey}/config/${item.key}`)}>
                      {item.icon}
                      {item.label}
                    </SidebarTab>
                  </NavLink>
                ))
              }
            </nav>
            <Switch>
              <Route path={`/${this.props.match.params.siteKey}/config/settings`} exact component={props=>(<SchemaEditCmp schemaKey="Settings" isSingular={true} />)}/>


              <Route path={`/${this.props.match.params.siteKey}/config`} exact component={props=>(<OverviewCmp menuStruct={this.menuStruct} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/blocks`} exact component={props=>(<SchemaListCmp schemaKey="Blocks" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/blocks/:id`} exact component={props=>(<SchemaEditCmp schemaKey="Blocks" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/fields`} exact component={props=>(<SchemaListCmp schemaKey="CustomFields" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/fields/:id`} exact component={props=>(<SchemaEditCmp schemaKey="CustomFields" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/pages`} exact component={props=>(<SchemaListCmp schemaKey="Pages" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/pages/:id`} exact component={props=>(<SchemaEditCmp schemaKey="Pages" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/types`} exact component={props=>(<SchemaListCmp schemaKey="CustomPostType" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/types/:id`} exact component={props=>(<SchemaEditCmp schemaKey="CustomPostType" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/scripts`} exact component={props=>(<SchemaListCmp schemaKey="Scripts" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/scripts/:id`} exact component={props=>(<SchemaEditCmp schemaKey="Scripts" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/styles`} exact component={props=>(<SchemaListCmp schemaKey="Styles" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/styles/:id`} exact component={props=>(<SchemaEditCmp schemaKey="Styles" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/taxonomies`} exact component={props=>(<SchemaListCmp schemaKey="Taxonomies" {...props} />)}/>
              <Route path={`/${this.props.match.params.siteKey}/config/taxonomies/:id`} exact component={props=>(<SchemaEditCmp schemaKey="Taxonomies" {...props} />)}/>
            </Switch>
          </>
        ):(<Pane width="100vw" height="calc(100vh - 50px)" display="flex" alignItems="center" justifyContent="center"><Busy/></Pane>)}
      </section>
    )
  }
}

export default PageConfig;
